@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap");
:root {
  --primary-color: #990055;
  --primary-font: "Poppins", sans-serif;
  --secondary-color: #ffc2b4;
  --primary-nav-color: #e21056;
  --primary-bg: rgb(153 0 51 / 14%);
  --font-color: #b7b7b7;
  --bg-color: #171717;
  --bg-dark-color: #0c0b0b;
  --white-color: #fdfffc;
  --green-color: #0abf53;
  --green-color-bg: rgb(10 191 83 / 17%);
  --blue-color: #6168ff;
  --blue-color-bg: rgb(97 104 255/9%);
  --border-color-light: rgb(255, 255, 255, 0.2);
  --box-shadow: 0 1rem 3rem rgb(0 0 0/12%);
  --dark-layer-color: linear-gradient(
    to top,
    rgb(21 21 21 /67%),
    rgb(23 23 23/17%)
  );
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  font-size: 14px;
  font-family: var(--primary-font);
  background-color: var(--bg-color);
}

::-webkit-scrollbar {
  width: 0.375rem;
}

::-webkit-scrollbar-track {
  background: var(--secondary-color);
}

::-webkit-scrollbar-thumb {
  background: var(--primary-color);
}

/*pre-defined classes css*/

a {
  text-decoration: none;
}

.container-fluid {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-right: 1rem;
  padding-left: 1rem;
}

.container {
  width: auto;
  max-width: 1320px;
  margin-left: auto;
  margin-right: auto;
  padding-right: 1rem;
  padding-left: 1rem;
}

.btn_wrapper {
  margin-top: 5rem;

  @media (max-width: 992px) {
    width: 100%;
    text-align: center;
    display: block;
  }

  @media (max-width: 767px) {
    margin-bottom: 3rem;
  }

  .btn {
    padding: 0.85rem 2.57rem;
    font-size: 1.14rem;
    border-radius: 2.14rem;
    font-weight: 600;
    box-shadow: 0 0.5rem 1rem rgb(16, 16, 16);
    outline: 2px, solid var(--primary-color);
    outline-offset: 0px;
    border: none;
    cursor: pointer;
    background: var(--primary-color);
    transition: all 0.3s ease-in-out;
    color: var(--white-color);
    &:hover {
      outline-offset: 10px;
    }
  }
}

h1 {
  font-size: 3.875rem;
  margin-bottom: 1.25rem;
  font-weight: 600;
  color: var(--white-color);
}
h2 {
  font-size: 2.85rem;
  font-weight: 600;
  color: var(--white-color);
}
h3 {
  font-size: 2.25rem;
  font-weight: 600;
  color: var(--white-color);
}
h4 {
  font-size: 1.5rem;
  line-height: 2.25rem;
  font-weight: 600;
  color: var(--white-color);
}

h5 {
  font-size: 1.5rem;
  line-height: 1.2rem;
  margin-bottom: 0.937rem;
  font-weight: 500;
  color: var(--white-color);
}

h6 {
  font-size: 0.875rem;
  text-transform: uppercase;
  margin-bottom: 0.937rem;
  font-weight: 300;
  color: var(--white-color);
}

p {
  font-size: 1rem;
  color: gray;
  line-height: 1.625rem;
}

